@import "styles/variables";
@import "styles/mixins";

.button {
  @include resetButton;
  @include regular($colorFontLightest, 700);

  position: relative;
  min-width: 20rem;
  height: 5rem;
  border-radius: 2.3rem;
  transition-duration: $transitionDuration;
}

.buttonLarge {
  height: 6rem;
  border-radius: 3rem;
}

.buttonFilled {
  color: $colorBgHighlightLighter;
  background-color: $colorBgLightest;
  border: 1px solid $colorBgLightest;

  &:hover,
  &:focus {
    color: $colorFontLightest;
    background-color: transparent;
  }

  &:active {
    color: $colorFontLightest;
    background-color: transparent;
    opacity: 0.7;
  }
}

.buttonBordered {
  color: $colorFontLightest;
  background-color: $colorBgLightestTransparent2;
  border: 1px solid $colorBgLightest;

  &:hover,
  &:focus {
    background-color: transparent;
  }

  &:active {
    color: $colorFontLightest;
    background-color: transparent;
    opacity: 0.7;
  }
}

.buttonDisabled {
  pointer-events: none;
}

.icon {
  position: absolute;
  top: calc(50% - 2.5rem / 2);
  right: 2rem;
  width: 2.5rem;
  fill: $colorFontLightest;
}
