@import "styles/variables";
@import "styles/mixins";

.map {
  width: 100%;
  height: 40rem;
  border-radius: 1rem;
}

.button {
  @include resetButton;

  position: absolute;
  left: 1.2rem;
  display: flex;
  justify-content: center;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: $colorBgLightest;
  box-shadow: 0 0.2rem 0.4rem rgba(0, 0, 0, 0.23);
}

.buttonIn {
  bottom: 6.7rem;

  .icon {
    width: 1.6rem;
  }
}

.buttonOut {
  bottom: 1.8rem;

  .icon {
    width: 1.5rem;
  }
}

.icon {
  fill: $colorFontHighlightLighter;
}

@media (max-width: $smartphoneMax) {
  .map {
    height: 100%;
  }
}
