@import "styles/variables";
@import "styles/mixins";

.message {
  @include regular($colorFontLightest);

  display: grid;
  grid-template-columns: 3rem auto;
  grid-column-gap: 1.5rem;
  justify-content: center;
}

.messageSuccess {
  align-items: center;
}

.icon {
  width: 100%;
  stroke: $colorFontLightest;
}
