@import "styles/variables";

.wrapper {
  --inner-padding-l: 65rem;
  padding-top: 7.5rem;
  padding-bottom: 15rem;
  padding-left: var(--inner-padding-l);
  background-size: 52rem 30rem;
  background-repeat: no-repeat;
  background-position: 0 center;
}

.list {
  margin-top: 6rem;
  padding-right: 10rem;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    --inner-padding-l: 49rem;
    padding-top: 0;
    padding-bottom: 7rem;
    background-size: 46.8rem 27.2rem;
  }

  .heading {
    font-size: 3.6rem;
    line-height: 4.2rem;
  }

  .list {
    margin-top: 3rem;
    padding-right: 0;
  }
}

@media (max-width: $smartphoneMax) {
  .wrapper {
    --inner-padding-l: 0;
    background-size: 29.2rem 17rem;
    background-position: center 4rem;
  }

  .heading {
    font-size: 2rem;
    line-height: 2rem;
  }

  .list {
    margin-top: 22rem;
  }
}
