@import "styles/variables";
@import "styles/mixins";

.heading {
  @include highlight($colorFontLightest);
}

.form {
  display: grid;
  grid-template-columns: 100%;
  grid-row-gap: 1.5rem;
  margin-top: 2rem;
}

.button {
  display: block;
  margin: 2rem 0 0 auto;
}

@media (max-width: $smartphoneMax) {
  .heading {
    font-size: 2rem;
    line-height: 2.4rem;
  }

  .button {
    margin: 1.6rem auto 0 auto;
  }
}
