@import "styles/variables";

.wrapper {
  margin: 0 auto;
  width: 144rem;
  padding-right: 8rem;
  padding-left: 8rem;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding-right: 3.2rem;
    padding-left: 3.2rem;
    width: 100%;
  }
}

@media (max-width: $smartphoneMax) {
  .wrapper {
    padding-right: 3.2rem;
    padding-left: 3.2rem;
  }
}
