@import "styles/mixins";

.wrapper {
  padding-top: 6.5rem;
}

.heading,
.description {
  position: relative;
  padding-right: 50%;
  padding-left: 11rem;
  z-index: 1;
}

.description {
  @include highlight;

  position: relative;
  margin: 2rem 0 0 0;
}

.icon {
  margin-top: -20rem;
  margin-bottom: -15rem;
  width: 100%;
  height: 80rem;
  transform: scaleX(-1);
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding-top: 8rem;
  }

  .heading,
  .description {
    padding-right: 20rem;
    padding-left: 8rem;
  }

  .description {
    margin-top: 3rem;
  }

  .icon {
    margin-top: -27rem;
    margin-bottom: -28rem;
  }
}

@media (max-width: $smartphoneMax) {
  .wrapper {
    padding-top: 4rem;
  }

  .heading,
  .description {
    padding-right: 7rem;
    padding-left: 0;
  }

  .description {
    margin-top: 1rem;
  }

  .icon {
    margin-top: -15rem;
    margin-bottom: -30rem;
    margin-left: -18.4rem;
    width: 78.4rem;
  }
}
