@import "styles/variables";
@import "styles/mixins";

.footer {
  background-color: $colorBgHighlightLighter;

  .wrapper {
    display: grid;
    grid-template-columns: 1fr 42rem 42rem;
    padding-top: 6rem;
    padding-bottom: 6rem;
  }

  .logo {
    width: 12rem;
    fill: $colorBgLightest;
  }

  .heading {
    @include highlight($colorFontLightest);
  }

  .list {
    @include resetList;

    margin-top: 2.5rem;
  }

  .item {
    &:not(:first-child) {
      margin-top: 2.5rem;
    }
  }

  .link {
    @include regular($colorFontLightest, 500);

    display: flex;
    align-items: center;
    transition-duration: $transitionDuration;

    &:hover,
    &:focus {
      opacity: 0.7;
    }
  }

  .iconContacts {
    margin-right: 1.5rem;
    width: 2rem;
    stroke: white;
    fill: white;
  }

  .apps {
    display: flex;
    flex-direction: column;
  }

  .store {
    margin-top: 2rem;
    font-size: 0;

    &:hover,
    &:focus {
      .icon {
        fill: $colorBgBullets;
      }
    }
  }

  .icon {
    width: 17rem;
    fill: $colorFontLightest;
    transition-duration: $transitionDuration;
  }

  .copy {
    @include regular($colorFontLightest);

    grid-column: 1 / -1;
    margin-top: 2.1rem;
    text-align: center;
  }
}

@media (max-width: $adaptiveMax) {
  .footer {
    .wrapper {
      display: grid;
      grid-template-columns: 1fr 32rem 30.5rem;
      padding-top: 4.5rem;
    }
  }
}

@media (max-width: $smartphoneMax) {
  .footer {
    .wrapper {
      display: flex;
      flex-direction: column;
      padding-top: 1.8rem;
      padding-bottom: 1.8rem;
    }

    .apps {
      margin-top: 4rem;
    }

    .form {
      margin-top: 4rem;
    }

    .heading {
      font-size: 2rem;
      line-height: 2.4rem;
    }

    .item {
      font-size: 1.6rem;
      line-height: 1;
    }

    .copy {
      margin-top: 3.5rem;
    }
  }
}
