@import "styles/variables";

.wrapper {
  display: grid;
  grid-template-columns: 47rem 1fr;
  grid-column-gap: 9rem;
  padding-left: 10.8rem;
}

.heading,
.description {
  grid-column: 1 / 2;
}

.image {
  grid-column: 2 / 3;
  grid-row: 1 / 4;
  width: 52rem;
  height: 39.8rem;
  object-fit: cover;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    grid-template-columns: 38rem 1fr;
    grid-column-gap: 2rem;
    padding-left: 8.5rem;
  }

  .image {
    width: 35rem;
    height: 27rem;
  }
}

@media (max-width: $smartphoneMax) {
  .wrapper {
    grid-template-rows: repeat(2, auto);
    grid-template-columns: 100%;
    grid-row-gap: 2rem;
    padding-left: 0;
  }

  .image {
    grid-column: 1 / 2;
    grid-row: 3 / 4;
    display: block;
    margin: 0 auto;
    width: 24.5rem;
    height: 18.7rem;
  }
}
