@import "styles/variables";
@import "styles/mixins";

.wrapper {
  display: grid;
  grid-template-columns: 45rem 1fr;
  grid-column-gap: 9rem;
  padding-left: 10rem;
}

.heading,
.description {
  grid-column: 1 / 2;
}

.description {
  margin-top: 4rem;
}

.list {
  @include resetList;

  grid-column: 2 / 3;
  grid-row: 1 / 4;
  display: flex;
  align-items: center;
}

.item {
  &:not(:first-child) {
    margin-left: -10rem;
  }
}

.image {
  width: 30rem;
}

.wrapperRTL {
  grid-template-columns: 1fr 45rem;
  padding-left: 0;

  .heading,
  .description {
    grid-column: 2 / 3;
  }

  .list {
    @include resetList;

    grid-column: 1 / 2;
    grid-row: 1 / 4;
  }
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    grid-template-columns: 40rem 1fr;
    grid-column-gap: 2rem;
    padding-left: 0;
  }

  .heading,
  .description {
    padding-left: 6rem;
  }

  .heading {
    font-size: 3.6rem;
    line-height: 1.15;
  }

  .list {
    .item {
      &:not(:first-child) {
        margin-left: -5.5rem;
      }
    }
  }

  .image {
    width: 19rem;
  }
}

@media (max-width: $smartphoneMax) {
  .wrapper {
    display: flex;
    flex-direction: column;
    grid-column-gap: 0;

    .heading,
    .description {
      padding-left: 0;
    }

    .heading {
      font-size: 2rem;
      line-height: 1;
    }

    .description {
      margin-top: 2.4rem;
    }

    .list {
      margin-top: 2.4rem;

      .item {
        &:first-child {
          margin-left: -3rem;
        }

        &:not(:first-child) {
          margin-left: -4.5rem;
        }
      }
    }

    .image {
      width: 17rem;
    }
  }
}
