@import "styles/variables";
@import "styles/mixins";

.wrapper {
  padding-top: 10rem;
  padding-bottom: 12rem;
}

.list {
  @include resetList;
}

.slogan {
  display: block;
  text-align: center;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding-top: 7rem;
    padding-bottom: 7rem;

    .slogan {
      font-size: 3.6rem;
      line-height: 1.15;
    }
  }
}

@media (max-width: $smartphoneMax) {
  .wrapper {
    padding-top: 5rem;
    padding-bottom: 2.5rem;

    .slogan {
      font-size: 2rem;
      line-height: 3rem;
    }
  }
}
