@import "styles/variables";
@import "styles/mixins";

.section {
  --padding-left: 65rem;
  position: relative;
  background-color: $colorBgHighlightLighter;
  background-repeat: no-repeat;
  background-size: 60rem auto;
}

.wrapper {
  padding-top: 9.6rem;
  padding-bottom: 13rem;
}

.heading {
  display: block;
  padding-left: var(--padding-left);
}

.list {
  @include resetList;

  display: flex;
  align-items: center;
  margin-top: 5.3rem;
  padding-left: var(--padding-left);
}

.item {
  font-size: 0;

  &:not(:first-child) {
    margin-left: 5rem;
  }
}

.iconApple,
.iconGoogle {
  height: 5.6rem;
  fill: $colorBgLightest;
  transition-duration: $transitionDuration;

  &:hover {
    fill: $colorBgBullets;
  }
}

@media (max-width: $adaptiveMax) {
  .section {
    --padding-left: 43.5rem;
    background-size: 45rem 32rem;

    .wrapper {
      padding-top: 4.5rem;
      padding-bottom: 4rem;
    }

    .heading {
      font-size: 3.6rem;
      line-height: 4.6rem;
    }

    .list {
      margin-top: 3.6rem;
    }

    .iconApple,
    .iconGoogle {
      height: 4.7rem;
    }
  }
}

@media (max-width: $smartphoneMax) {
  .section {
    --padding-left: 0;
    background-size: 0;

    .wrapper {
      padding-top: 3rem;
    }

    .heading {
      font-size: 2rem;
      line-height: 3rem;
    }

    .list {
      flex-direction: column;
      align-items: flex-start;
    }

    .item {
      &:not(:first-child) {
        margin-left: 0;
        margin-top: 2.5rem;
      }
    }
  }
}
