@import "styles/variables";
@import "styles/mixins";

.heading + .list {
  margin-top: 2.7rem;
}

.list {
  @include resetList;
}

.item {
  @include regular($colorFontRegular, 500);

  position: relative;
  padding-left: 1.8rem;

  &::before {
    --width: 1rem;
    content: "";
    position: absolute;
    top: calc(50% - var(--width) / 2);
    left: 0;
    width: var(--width);
    height: var(--width);
    background-color: $colorBgBullets;
    border-radius: 50%;
  }

  &:not(:first-child) {
    margin-top: 1.6rem;
  }
}

.listStriped {
  .item {
    padding: 1.1rem 1.6rem 1.1rem 2rem;
    line-height: 2.4rem;
    background-color: $colorBgLightest;

    &::before {
      top: 0;
      left: 0;
      width: 0.3rem;
      height: 100%;
      background-color: $colorBgDarkest;
      border-radius: 0;
    }
  }
}

@media (max-width: $smartphoneMax) {
  .heading {
    margin-top: 2.7rem;
  }

  .heading + .list {
    margin-top: 2.2rem;
  }
}
