@import "styles/mixins";
@import "styles/variables";

.wrapper {
  position: relative;
  padding-top: 15rem;
  padding-bottom: 7.5rem;

  :global {
    .swiper {
      &-wrapper {
        @include resetList;

        padding-bottom: 3rem;
      }

      &-pagination.swiper-pagination-bullets {
        bottom: 0;
      }
    }
  }
}

.button {
  @include resetButton;

  --size: 4rem;
  position: absolute;
  top: calc(50% - var(--size) / 2);
  width: var(--size);
  height: var(--size);
  background-color: $colorFontHighlightLighter;
  transition-duration: $transitionDuration;
  z-index: 1;

  &:not(.buttonNext) {
    left: 0;
    transform: rotate(180deg);
  }
}

.buttonNext {
  right: 0;
}

.buttonDisabled {
  background-color: $colorBgLighter2;
  pointer-events: none;
}

.icon {
  width: 2.2rem;
  fill: $colorFontLightest;
}

.bullet {
  display: inline-block;
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 0;
  background-color: $colorBgLighter2;
  cursor: pointer;

  &:not(:first-child) {
    margin-left: 1.2rem;
  }
}

.bulletActive {
  background-color: $colorBgHighlightLighter;
  pointer-events: none;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding-top: 8rem;
    padding-bottom: 13.2rem;
  }
}

@media (max-width: $smartphoneMax) {
  .wrapper {
    padding-top: 5rem;
    padding-bottom: 5rem;
  }

  .button {
    top: 21.1rem;
  }
}
