@import "styles/variables";

.section {
  position: relative;
  padding-top: 9.3rem;
  padding-bottom: 11.5rem;
  min-height: 61.3rem;
  background-color: $colorFontHighlightLighter;
}

.icon {
  position: absolute;
  bottom: 0;
  fill: rgba(255, 255, 255, 0.08);
  transform: scale(-1, 1);
}

.heading {
  text-align: center;
}

.form {
  position: relative;
  display: grid;
  grid-template-columns: 100%;
  grid-row-gap: 2.3rem;
  width: 63rem;
  margin: 4.6rem auto 0 auto;
}

.doubleColumn {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-column-gap: 2rem;
}

.button {
  margin: 2.2rem auto 0 auto;
  width: 41.5rem;
}

@media (max-width: $adaptiveMax) {
  .icon {
    bottom: 1rem;
    width: 100%;
  }

  .form {
    width: 47rem;
  }

  .button {
    margin: 4.1rem auto 0 auto;
    width: 30.5rem;
  }
}

@media (max-width: $smartphoneMax) {
  .section {
    padding-top: 3rem;
    padding-bottom: 7rem;
  }

  .heading {
    font-size: 2rem;
    line-height: 3rem;
  }

  .form,
  .doubleColumn {
    grid-template-columns: 100%;
    grid-row-gap: 1.5rem;
    width: 100%;
  }

  .button {
    margin: 3rem 0 0 0;
    width: 100%;
  }

  .icon {
    left: -17.8rem;
    width: 78.4rem;
  }
}
