@import "styles/variables";
@import "styles/mixins";

.section {
  background-color: $colorBgHighlightLighter;
}

.wrapper {
  display: grid;
  grid-template-columns: 66.6rem 1fr;
  grid-column-gap: 2rem;
  padding-top: 3rem;
  padding-bottom: 3rem;
  border-bottom: 1px solid $colorBgLightestTransparent1;
  align-items: flex-start;
}

.heading {
  @include visuallyHidden;
}

.stats {
  grid-column: 2 / 3;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3rem 0 8.3rem 0;
}

.list {
  display: flex;
  align-items: center;
  margin-top: 5rem;
}

.item {
  @include h2($colorFontLightest);

  position: relative;
  line-height: 4.7rem;
  text-indent: 8rem;

  &:not(:first-child) {
    margin-left: 11rem;
  }

  span {
    font-weight: 400;
  }
}

.itemInhabitants {
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 3rem;
    width: 3rem;
    height: 4rem;
    background-image: url("/assets/icons/human.svg");
    background-size: cover;
    background-repeat: no-repeat;
  }
}

.itemBuildings {
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 3rem;
    width: 3.5rem;
    height: 4rem;
    background-image: url("/assets/icons/building.svg");
    background-size: cover;
    background-repeat: no-repeat;
    z-index: 1;
  }
}

.slogan {
  margin-top: 7rem;
}

.map {
  position: relative;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    grid-template-columns: 46.9rem 1fr;
    padding-bottom: 5rem;
  }

  .stats {
    padding: 1.2rem 0 0 0;
  }

  .item {
    font-size: 3.6rem;
    line-height: 4.3rem;

    &:not(:first-child) {
      margin-left: 7.6rem;
    }
  }
}

@media (max-width: $smartphoneMax) {
  .wrapper {
    display: flex;
    flex-direction: column;
    padding: 2rem 2rem 3rem 2rem;
  }

  .map {
    width: 100%;
    height: 30rem;
  }

  .stats {
    margin-top: 4rem;
  }

  .sloganTop,
  .slogan {
    font-size: 2rem;
    line-height: 2.4rem;
  }

  .item {
    font-size: 2rem;
    line-height: 2.4rem;


    &:not(:first-child) {
      margin-left: 10rem;
    }

    span {
      margin-left: 4rem;
    }
  }

  .itemInhabitants {
    &::before {
      left: 5.5rem;
      width: 1.5rem;
      height: 2rem;
    }
  }

  .itemBuildings {
    &::before {
      left: 5rem;
      width: 2rem;
      height: 2rem;
    }
  }
}
