@import "styles/variables";
@import "styles/mixins";

.item {
  @include regular($colorFontDarkest);

  padding: 2.1rem 2.1rem 2rem 2.1rem;
  min-width: 20.6rem;
  font-size: 1.2rem;
  line-height: 1.4rem;
  text-align: center;
  transition-duration: $transitionDuration;
  cursor: pointer;
}

.itemActive {
  color: $colorFontHighlightDarker;
  background-color: $colorBgDarkestTransparent;
  pointer-events: none;
}

@media (max-width: $smartphoneMax) {
  .item {
    padding: 1.3rem 0;
    min-width: 17rem;
  }
}
