@import "styles/variables";
@import "styles/mixins";

.extWrapper {
  position: absolute;
  left: 0;
  width: 100%;
  background-color: $colorBgMain;
  transition-duration: $transitionDuration;
  transform: translateY(0);
  z-index: 5;
}

.wrapper {
  display: flex;
  align-items: center;
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.logo {
  display: flex;
  width: 16.5rem;
  fill: $colorFontHighlightDark;
}

.selector {
  margin-left: 26.8rem;
}

.buttonLogo,
.buttonContacts {
  @include resetButton;
}

.buttonContacts {
  @include regular($colorFontDarkest);

  margin-left: auto;
}

@media (max-width: $adaptiveMax) {
  .logo {
    width: 12.7rem;
  }

  .selector {
    margin-left: 14.5rem;
  }
}

@media (max-width: $smartphoneMax) {
  .wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, auto);
    grid-row-gap: 3rem;
  }

  .logo {
    grid-column: 1 / 2;
    grid-row: 1  / 2;
    width: 8.2rem;
  }

  .buttonContacts {
    grid-column: 2 / 3;
    grid-row: 1  / 2;
  }

  .selector {
    grid-column: 1 / -1;
    grid-row: 2  / 3;
    margin: 0 auto;
  }
}
