@import "styles/variables";
@import "styles/mixins";

.wrapper {
  display: flex;
  flex-direction: column;
  padding: 6.5rem 3rem 4.7rem 3rem;
  background-color: $colorBgLightest;

  .heading {
    text-align: center;
    text-transform: uppercase;
  }

  .price {
    @include h3($colorFontHighlightLighter);

    margin-top: 2rem;
    text-align: center;
  }

  .description {
    margin-top: 6rem;
  }
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding: 3.2rem 2rem 2rem 2rem;

    .price {
      margin-top: 1rem;
    }

    .description {
      margin-top: 2rem;
    }
  }
}

@media (max-width: $smartphoneMax) {
  .wrapper {
    .price,
    .heading {
      font-size: 2.4rem;
      line-height: 2.9rem;
    }

    .description {
      font-size: 1.6rem;
      line-height: 2.4rem;
    }
  }
}
