@import "styles/variables";

.wrapper {
  display: grid;
  grid-template-columns: 43.4rem 1fr;
}

.wrapperImageRight {
  grid-auto-flow: dense;

  & > *:first-child {
    grid-column: 2 / 3;
    margin-left: auto;
  }

  .list {
    grid-column: 1 / 2;
  }
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    grid-template-columns: 33rem 1fr;
  }
}

@media (max-width: $smartphoneMax) {
  .wrapper {
    display: flex;
    flex-direction: column;
  }

  .wrapperImageRight {
    & > *:first-child {
      margin-left: 0;
    }
  }
}
