@import "styles/variables";
@import "styles/mixins";

.wrapper {
  padding: 12.6rem 29.6rem 7.5rem 29.6rem;
}

.heading {
  text-align: center;
}

.list {
  @include resetList;

  --gap: 6rem;
  display: grid;
  grid-template-columns: 100%;
  grid-row-gap: var(--gap);
  padding-top: var(--gap);
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding: 6rem 5.5rem 9rem 11rem;
  }
}

@media (max-width: $smartphoneMax) {
  .wrapper {
    padding: 6rem 3.2rem;
  }
}
