@import "styles/variables";
@import "styles/mixins";

.fieldset {
  @include resetFieldset;
}

.label {
  @include regular($colorFontLightest, 500);
}

.label + .input {
  margin-top: 0.65rem;
}

.input {
  @include regular($colorFontLightest, 500);

  margin: 0;
  padding: 0 1rem;
  height: 5rem;
  width: 100%;
  background-color: $colorBgLightestTransparent1;
  transition-duration: $transitionDuration;
  border-radius: 1rem;
  border: none;

  &:hover,
  &:focus {
    background-color: $colorBgLightestTransparent;
  }
}

.inputLarge {
  height: 6rem;
}

.inputBordered {
  border: 1px solid $colorFontLightest;
}

.inputError {
  outline: 2px solid orange;

  &:hover,
  &:focus {
    outline: 2px solid orange;
  }
}
