.header {
  &HiddenFixed {
    position: fixed;
    top: -100%;
  }

  &HiddenFixedTransform {
    position: fixed;
    transform: translateY(-100%);
  }

  &VisibleFixed {
    position: fixed;
    top: 0;
    transform: translateY(0);
  }

  &HiddenAbsolute {
    position: absolute;
    transform: translateY(-100%);
  }
}
