@import "styles/mixins";
@import "styles/variables";

.heading {
  text-align: center;
}

.list {
  @include resetList;

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2rem;
  margin-top: 6rem;
  padding-bottom: 15rem;
}

@media (max-width: $adaptiveMax) {
  .list {
    margin-top: 4rem;
    padding-bottom: 8rem;
  }
}

@media (max-width: $smartphoneMax) {
  .list {
    grid-template-columns: 100%;
    grid-template-rows: repeat(3, auto);
    grid-row-gap: 2rem;
    margin-top: 2rem;
    padding-bottom: 6rem;
  }
}
