@keyframes moveClouds {
  0% {
    transform: translateX(0);
  }

  33% {
    transform: translateX(3rem);
  }

  66% {
    transform: translateX(-3rem);
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes moveBike {
  0% {
    transform: translateX(-200rem);
  }

  100% {
    transform: translateX(40rem);
  }
}

@keyframes rotateSun {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes moveBalloon {
  0% {
    transform: translateX(0) translateY(0);
  }

  25% {
    transform: translateX(3rem) translateY(-10rem);
  }

  50% {
    transform: translateX(0) translateY(-20rem);
  }

  75% {
    transform: translateX(-3rem) translateY(-10rem);
  }

  100% {
    transform: translateX(0) translateY(0);
  }
}

#city-clouds {
  animation-name: moveClouds;
  animation-duration: 10s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in;
}

#city-bike {
  animation-name: moveBike;
  animation-duration: 10s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

#city-sun {
  animation-name: rotateSun;
  animation-duration: 120s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  transform-origin: center 300%;
}

#city-balloon {
  animation-name: moveBalloon;
  animation-duration: 40s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-timing-function: ease-out;
}
